
.MainPage{
    width: 100%;
    background-color: lightgrey;
    float: left;
    padding: 10px;
    z-index: 1000;
}
.inputx{
    padding: 10px;
    display: inline-block;
}
.inputx > input{
    margin-right: 10px;
    padding: 10px;
    display: inline-block;
    text-align: center;
}
.Line{
    margin: 20px;
}
@media screen and (max-width: 750px) {
    .MainPage {
      width: 100%;
    }
  }
.inputx input, .inputx select{
    width: 100%;
    margin-bottom: 5px;
}
.inputx textarea{
    padding: 5px;
}
.labeltextlabel{
    padding: 10px;
    color: aliceblue;
}
.labelbig, .label{
    text-align: center;
    background-color: #007bff;
}
.SellerData input, .TargetData input{
    margin-bottom: 5px;
}
.TargetData, .SellerData{
    margin-left: 5px;
}
.textarea{
    min-height: 40px;
    width: 100%;
}
.blank{
    height: 60px;
    width: 100%;
}
.TotalText{
    font-weight: bold;
    text-align: right;
    margin-bottom: 70px;
    padding-top: 10px;
}
.generatebutton{
    width: 100%;
    height: 50%;
}

.leftheader{
    float: left;
}
.rightheader{
    float: right;
}
.look{
    padding: 100px;
}
.headerlook{
    padding: 20px;
    height: 90px;
    background-color: rgb(229, 226, 226);
    border-top: 3px solid grey;
    border-bottom: 3px solid grey;
}
.sectionOneData{
    margin-top: 20px;
    float: right;
}
.SellerData1, .BuyerData1{
    margin-top: 50px;
    float: left;
}
.labelbig1, .label1, .Line1{
    text-align: center;
    background-color: lightgrey;
    padding: 5px;
    color:black;
    margin: 0;
}
.SectionThree,.SectionFour,.Sectionfive,.Sectionsix{
    border: 3px solid grey;
}
.label1,.labelbig1{
    border-right: 3px solid grey;
}
.labelbigtext1, .labeltext1, .Linedata1{
    text-align: center;
    background-color: white;
    padding: 15px 5px 15px 5px;
    color:black;
    margin: 0;
    border-right: 3px solid grey;
    font-size:20px;
}
.labelsmall{
    padding: 10px 0px 10px 0px;
}
.fontbold{
    font-weight: bold;
}
.Sectionseven{
    margin-top: 20px;
}
.Sectioneight{
    margin-top: 100px;
}
.dotted{
    border:1px dotted black;
    height: 1px;
    width: 100%;
}
.center{
    text-align: center;
}
.Menu{
    height: 100%;
    width: 10%;
    position: fixed;
    background: #007bff;
    padding: 1%;
    text-align: center;
}
.list{
    width: 100px;
    height: 100px;
}

.App{
  padding: 50px;
}
.none{
  position: absolute;
  z-index: 0;
  width:90%;
  float:right;
}
.block{
  position: relative;
  z-index: 1000;
  display: block;
  width:90%;
  float:right;
}
.blocknone{
  display: none;
}
.positionfixed{
  font-weight: bold;
  font-size: 0.8vw;
  width: 10vw;
  position: fixed;
  top:30px;
  left:45%;
  z-index: 1000;
}
